import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { BannerListComponent } from './banner-list.component';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { BannerService } from 'src/app/service/banner.service';

const routes: Routes = [
  {
    path: '',
    component: BannerListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../banner-add/banner-add.module').then(m => m.BannerAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../banner-add/banner-add.module').then(m => m.BannerAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    BannerListComponent
  ],
  imports: [
    CommonModule,
    LoderModule,
    PaginationModule,
    RouterModule.forChild(routes)
  ],
  providers: [
   BannerService
  ]
})
export class BannerListModule { }